import '../../App.css';
import self from '../../images/self.jpg'

function Home() {
    return (
        <div class='home'>

            <table width="100%">
                <tr>
                    <td><img src={self} alt="picture of myself" width="230px" height="300px" style={{
                        width: 230,
                        height: 300,
                        borderWidth: 10
                    }} /></td>
                    <td valign="top">
                        <h1>Hello!</h1>
                        <br></br>
                        <p>My name is Hana.  I created this website to expand my skills using React and CSS,
                        and to give a bit more information about myself and my skills.
                <br></br>
                In my spare time, I enjoy spending time outdoors, taking care of my many plants, and reading
                -- Donna Tartt's <i>The Secret History</i> is a personal favorite.  I also
                enjoy learning new programming languages to expand my CS knowledge.</p>
                    </td>
                </tr>
            </table>


        </div>
    )
}

export default Home;